.single-movie-details
  display: flex
  flex-direction: column
  gap: 2rem
  h1
    margin: 0 0 1rem 0
  .videos > div
    display: flex
    flex-direction: row
    gap: 2rem
    justify-content: space-between
    iframe
      border: 1px solid $secondary-font-color
      border-radius: 10px
      filter: drop-shadow(0 0 1.5rem black)
      flex-grow: 1
      aspect-ratio: 16/9
  .casting > div
    display: flex
    flex-direction: row
    width: 100%
    height: fit-content
    gap: 1rem
    flex-grow: 1
    justify-content: space-between
    div
      filter: drop-shadow(0 0 1rem black)
      height: 100%
      border-radius: 10px
      background-color: $secondary-background
      color: $main-font-color
      outline: 1px solid $secondary-font-color
      width: 100%
      img
        border-radius: inherit
        border-bottom-right-radius: 0
        border-bottom-left-radius: 0
        width: 100%
        margin: 0
      p
        background: none
        margin: 0 10px 10px 10px
        &:first-of-type
          font-weight: 700
          margin-bottom: 0
        &:last-of-type
          font-weight: 400
          margin-top: 0
  .details
    display: flex
    gap: 0 2rem
    img
      display: flex
      width: 30%
      border-radius: 20px
      filter: drop-shadow(0 0 1.5rem black)
      aspect-ratio: 2/3
      flex-grow: 1
      flex-shrink: 1
    .side-info
      display: flex
      flex-direction: column
      gap: 1rem
      height: 100%
      max-width: 75%
      flex-shrink: 1
      .title
        h1
          margin: 0
          font-size: 3rem
      .genres
        display: flex
        flex-direction: row
        gap: 1rem
        p
          font-size: 0.8rem
          text-align: center
          font-weight: 600
          border: 2px solid $main-font-color
          background-color: $main-font-color
          color: $secondary-background
          border-radius: 9999px
          padding: 10px 30px
      .review
        .progress-circle
          display: flex
          align-items: center
          justify-content: center
          height: 4.5rem
          width: 4.5rem
          border-radius: 50%
          position: relative
          background: $secondary-font-color
          &::after
            position: absolute
            border-radius: inherit
            background: $secondary-background
            content: ""
            height: 90%
            width: 90%
          .fill
            position: absolute
            background: conic-gradient(#0f0 0, transparent 0deg)
            height: 100%
            width: 100%
            border-radius: 50%
            background: $secondary-font-color
        h3
          z-index: 10
          position: absolute
          background: none
          text-align: center
          font-size: 1.5rem
      .overview
        width: 60%

      div
        display: flex
        flex-direction: column
        h1
          word-wrap: break-word
        span
          color: grey
        .progress-bar
          filter: drop-shadow(0 0 0.5rem rgba(0, 0, 0, 0.5))
          width: 100%
          height: 1.2rem
          background-color: #373737
          padding: 3px
          border-radius: 9999px
          z-index: 10
          .fill
            height: 1.5rem
            filter: drop-shadow(0 0 0.2rem rgb(0, 255, 0.3))
            background-color: #0F0
            border-radius: inherit
            z-index: 0
        &.row
          display: flex
          flex-direction: row !important
          align-items: center
          gap: 2rem
          margin: 30px 0